@import '../../components/app/styles.scss';

$first-color: #F7121E;
$second-color: #090809;
$third-color: #FFF215;

.home-container {
    position: relative;
    height: 100vh; 
    overflow: hidden;
    
    #background-video {
        position: relative;
        z-index: 0;
    }

    .overlay {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute; 
        z-index: 10;
        top: -10px;
        margin: 0 auto;
        width: 100%;
        height: 100%;
        animation: show 1s ease-in-out;

        h1 {
            font-size: 6rem;
        }
    
        h3 {
            opacity: 0;
            animation: text .5s .5s ease-in-out forwards;
            max-width: 60%;
            font-size: 1.5rem;
            font-weight: 300;
        }
    
        .ferrari {
            position: absolute;
            height: 500px;
            bottom: 300px;
            right: 5vw;
            animation: show 1s ease;
        }
    }
}

@keyframes title {
    0% {
        transform: translateX(-100px);
        opacity: 0;
    }

    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes text {
    0% {
        transform: translateY(-50px);
        opacity: 0;
    }

    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

@media screen and (max-width: 1100px) {
    .home-container {
        overflow: hidden;
        height: 100vh;
        .overlay {
            display: flex;
            align-items: flex-start;
            position: absolute; 
            z-index: 10;
            top: 200px;

            img {
                z-index: 99;
                width: 90vw;
            }
        }
    }
}